        model : "",
        deliArea : "",
        adultYn : "",
      },

      /* shopInfo */
      shopAddress : "",
      shopNumber : "",
      shopName : "",
      shopSellerName : "",
      shopMemberNo : "",
      shopLoginId : "",
      shopAccountNo : "",
      shopAccountId : "",
      shopMailSeq : "",
      shopDefaultChannelNo : "",

       /* dome_detail */
       no : "",
       status : "",
       nego : "",
       adult : "",

