    ...mapGetters([
      'userInfo'
    ])
  },

  mounted () {
    axios.post('/api/category/v1/smartstore1',{
    }).then(res => {
      this.cateList1 = res.data;
      this.cateList2 = null;
      this.cateList3 = null;
      this.cateList4 = null;
    })
  },

  components: {
    DomePagination,
    DbPagination,
    StoragePagination
  },

  created () {
    this.domeListSend();
    this.dbListSend();
    //this.storageListSend();
  },
  filters:{
    'domeChangeDeli'(value){
      if(value.toLowerCase() == "s"){
        return "";
      }else if(value.toLowerCase() == "p"){
        return "";
      }else if(value.toLowerCase() == "b"){
        return "";
      }else if(value.toLowerCase() == "c"){
        return "(/)";
      }
    },
    'dbChangeDeli'(value){
      if(value.toLowerCase() == "f"){
        return "";
      }else if(value.toLowerCase() == "s"){
        return "";
      }else if(value.toLowerCase() == "g"){
        return "";
      }else if(value.toLowerCase() == "h"){
        return "(/)";
      }
    },
    'prodStatus'(value){
      if(value == "1"){
        return "";
      }else if(value == "8"){
        return "";
      }else{
        return "";
      }
    },
  },
  methods: {
    // #####################################################   START ##############################################################
    menuTabClick: function(item,idx) {
      this.currentTab = idx;
      if(this.currentTab == 0){
        if(this.selectedShop =="Db"){
          EventBus.$emit("dbPaginationInfo",this.dbPagination);
        }else if(this.selectedShop =="Dome"){
          EventBus.$emit("domePaginationInfo",this.domePagination);
        }
      }else if(this.currentTab == 2){
        this.storageListSend();
      }
    },
    // #####################################################   END #################################################################
    // #####################################################  () (currentTab = 0 ) START ################################
    domeListSend : function(index){
      axios.post('/api/product-manage/v1/dome-list',{
        pageIndex : index ? index : this.domePagination.pageIndex,
        pageSize : this.domePagination.pageSize
      }).then(res => {
        this.domeProductList = res.data.domeggook.list.item;
        this.domePagination.totalCount = res.data.domeggook.header.numberOfItems;
        this.domePagination.pageSize = res.data.domeggook.header.itemsPerPage;
        this.domePagination.pageIndex = res.data.domeggook.header.currentPage;
        EventBus.$emit("domePaginationInfo",this.domePagination)
      }).catch(res => {
        if(res.response.status =="401" || res.response.status =="302"){
          alert("  .");
          this.$router.go('/');
        }else{
          this.modalMessage=res.response.data;
          this.isModalViewed=true;
        }
      })
    },
    //   
    domeTabClick: function(domeproduct,idx) {
      this.domeSelectedTab = idx;
      if(this.dome_check.includes(domeproduct)){
          this.dome_check.splice(this.dome_check.indexOf(domeproduct),1);
      }else{
          this.dome_check[idx] = domeproduct;
      }
    },
    shopInfoConvert : function(shopInfo){
      this.shopDefaultChannerNo = shopInfo.represent.businessAddressInfo.fullAddressInfo; //  2 37 () 210 2404 ( : 22732) 
      this.shopNumber =  shopInfo.defaultChannel.contactInfo.telNo.formattedNumber; //010-9224-8577
      this.shopName = shopInfo.represent.representName; //
      this.shopSellerName = shopInfo.represent.representativeName; //
      this.shopMemberNo = shopInfo.memberNo;
      this.shopLoginId = shopInfo.member.loginId;
      this.shopAccountNo = shopInfo.id;
      this.shopAccountId = shopInfo.accountId;
      this.shopMailSeq = shopInfo.mallSeq;
      this.shopDefaultChannerNo = shopInfo.defaultChannelNo; //
    },
    registerResult : function(result){
      this.registerId = result.productId;
      return(this.registerId);
    },
    codeConvert : function (domeproduct){
      for(let inData in domeproduct){
        this.dome_detail_cd = domeproduct[inData].no
      }
      return this.dome_detail_cd;
    },
    domeDetailConvert : function (domeproduct){
      this.no = domeproduct.domeggook.basis.no;
      this.status = domeproduct.domeggook.basis.status;
      this.nego = domeproduct.domeggook.basis.nego;
      this.adult = domeproduct.domeggook.basis.adult;
    },
    domeConvert : function (domeproduct){
      let result = [];
      for(let inData in domeproduct){
        let deliFalg ;
        if(domeproduct[inData].deli.who == "S"){
          deliFalg = "f";
        }else if(domeproduct[inData].deli.who == "P"){
          deliFalg = "s";
        }else if(domeproduct[inData].deli.who == "B"){
          deliFalg = "g";
        }else if(domeproduct[inData].deli.who == "C"){
          deliFalg = "h";
        }
        let adultYn ;
        if(domeproduct[inData].adultOnly == "true"){
          adultYn = "Y";
        }else{
          adultYn = "N";
        }
        let deliPrice ;
        if(domeproduct[inData].deli.fee == "false"){
          deliPrice = 0;
        }else{
          deliPrice = domeproduct[inData].deli.fee;
        }
        let data = {
          "adultYn" : adultYn,
          "partnerCd" : "",
          "deliFlag" : deliFalg,
          "deliPrice" : deliPrice,
          "userId" : domeproduct[inData].id,
          "prodCd" : domeproduct[inData].no,
          "prodPrice" : domeproduct[inData].price,
          "listImg" : domeproduct[inData].thumb,
          "prodNm" : domeproduct[inData].title,
          "minOrderLimit" : domeproduct[inData].unitQty
        }
        result.push(data);
      }
      return result;
    },
    emptyConvert : function (product){
      var data = {
        "userId" : "SellerAll",
        "partnerCd" : "SellerAll",
        "prodCd" : product.prodCd,
        "prodNm" : product.prodNm,
        "uprodNm" : product.uprodNm,
        "categoryCd" : this.selectedCate1.categoryCd,
        "categoryLCd" : this.selectedCate2.categoryCd,
        "categoryLNm" : this.selectedCate2.categoryNm,      
        "categoryMCd" : this.selectedCate3.categoryCd,
        "categoryMNm" : this.selectedCate3.categoryNm,      
        "categorySCd" : this.selectedCate4.categoryCd,
        "categorySNm" : this.selectedCate4.categoryNm,
        "content" : product.content,
        "listImg" : product.listImg,
        "prodPrice" : product.prodPrice,
        "uprodPrice" : product.uprodPrice,
        "margin" : product.margin,
        "invenCnt" : product.invenCnt,
        "umadePlace" : product.umadePlace,
        "maker" : product.maker,
        "ubrand" : product.ubrand,
        "model" : product.model,
        "deliArea" : product.deliArea,
        "adultYn" : product.adultYn,
        "deliFlag" : "s",
        "deliPrice" : 2500,
        "minOrderLimit" : 1
      };

      return data;
    },
