        img1 = storageproduct[inData].img1;
        margin = storageproduct[inData].margin;

        /*
        if(categorySCd == null || categorySCd == ""){
          if(categoryMCd == null || categoryMCd == ""){
            if(categoryLCd == null || categoryLCd == ""){
              level = 1;
