    dbListSend : function(index){
      axios.post('/api/product-manage/v1/db-list',{
        pageIndex : index ? index : this.dbPagination.pageIndex,
        pageSize : this.dbPagination.pageSize
      }).then(res => {
        this.dbProductList = res.data.resultList;
        this.dbPagination.totalCount = res.data.totalCount;
        this.dbPagination.pageSize = res.data.pageSize;
        this.dbPagination.pageIndex = res.data.pageIndex;
        EventBus.$emit("dbPaginationInfo",this.dbPagination)
      }).catch(res => {
        if(res.response.status =="401" || res.response.status =="302"){
          alert("  .");
          this.$router.go('/');
        }else{
          this.modalMessage=res.response.data;
          this.isModalViewed=true;
        }
      })
    },
    //   
    dbTabClick: function(dbproduct,idx) {
      this.dbSelectedTab = idx;
      if(this.db_check.includes(dbproduct)){
          this.db_check.splice(this.db_check.indexOf(dbproduct),1);
      }else{
          this.db_check[idx] = dbproduct;
      }
    },
    productTabChange(event) {
      //console.log(event.target.value)
      if(event.target.value =="Db"){
        EventBus.$emit("dbPaginationInfo",this.dbPagination);
      }else if(event.target.value =="Dome"){
        EventBus.$emit("domePaginationInfo",this.domePagination);
      }
    },
    storageInit : function(){
        this.dome_check=[];
        this.db_check=[];
    },
    // 
    storageRegister : function(){
      if(this.selectedShop == "Dome"){
        if(this.dome_check == null || this.dome_check == ""){
          alert(" .");
        }else{
          axios.post('/api/product-manage/v1/storage-product',this.domeConvert(this.dome_check)).then(res => {
            alert(" ");
          }).catch(res => {
            if(res.response.status =="401" || res.response.status =="302"){
              alert("  .");
              this.$router.go('/');
            }else{
              this.modalMessage=res.response.data;
              this.isModalViewed=true;
            }
          })
          /*dome_detail
          axios.post('/api/product-manage/v1/dome-detail', this.codeConvert(this.dome_check)).then(res => {
            this.domeDetailConvert(res.data);
          })
          */
        }
      } else if(this.selectedShop == "Db"){
        if(this.db_check == null || this.db_check === ""){
          alert(" .");
        }else{
          axios.post('/api/product-manage/v1/storage-product',this.db_check).then(res => {
            alert(" ");
          }).catch(res => {
            if(res.response.status =="401" || res.response.status =="302"){
              alert("  .");
              this.$router.go('/');
            }else{
              this.modalMessage=res.response.data;
              this.isModalViewed=true;
            }
          })
        }
      } else{
        alert("");
      }
    },
