      alert(item.categoryLNm);
      alert(item.prodNm); //
      alert(item.prodStatus);
      alert(item.prodPrice); //
      alert(item.uprodPrice); //
      alert(item.margin); //
      alert(item.invenCnt);
      alert(item.uptDate);
      */
    },
    storageTabClick  : function(storageProduct,idx){
      this.storageSelectedTab = idx;
      this.modStorage(storageProduct);
      if(this.storage_check.includes(storageProduct)){
          this.storage_check.splice(this.storage_check.indexOf(storageProduct),1);
      }else{
          this.storage_check[idx] = storageProduct;
      }
