    storageListSend : function(index){
      axios.post('/api/product-manage/v1/storage-list',{
        pageIndex : index ? index : this.storagePagination.pageIndex,
        pageSize : this.storagePagination.pageSize
      }).then(res => {
        this.storageProductList = res.data.resultList;
        this.storagePagination.totalCount = res.data.totalCount;
        this.storagePagination.pageSize = res.data.pageSize;
        this.storagePagination.pageIndex = res.data.pageIndex;
        EventBus.$emit("storagePaginationInfo",this.storagePagination);
      }).catch(res => {
        if(res.response.status =="401" || res.response.status =="302"){
          alert("  .");
          this.$router.go('/');
        }else{
          this.modalMessage=res.response.data;
          this.isModalViewed=true;
        }
      })
    },
    storageCheckInit : function(){
        this.storage_check=[];
        this.storageModFlag = false;
    },
    storageDeleteSend : function(){
      let deleteParam = [];
      for(let data in this.storage_check){
        deleteParam.push({ prodIdx : this.storage_check[data].prodIdx });
      }
      axios.post('/api/product-manage/v1/storage-product-del',deleteParam).then(res => {
        this.storageListSend();
        alert(" ");
      }).catch(res => {
        if(res.response.status =="401" || res.response.status =="302"){
          alert("  .");
          this.$router.go('/');
        }else{
          this.modalMessage=res.response.data;
          this.isModalViewed=true;
        }
      })
    },
    saveStore : function(){
      if(this.storage_check == null || this.storage_check == ""){
        alert(" .");
        return;
      }

      /*
      if(this.storage_check.categoryLNm == null || this.storage_check.categoryLNm == ""){
        alert(" .");
        return;
      }
      */

      //  
      axios.post('/api/product-manage/v1/info-stp',{
      }).then(res => {
        this.shopInfoConvert(res.data);
      })

      for(let inData in this.storage_check){
        this.storage_check_test = this.storage_check[inData];
        this.storage_prodIdx = this.storage_check_test.prodIdx;
        this.storage_prodCd = this.storage_check_test.prodCd;+

        axios.post('/api/product-manage/v1/register-stp', this.storageDomeConvert(this.storage_check_test)).then(res => {
          axios.post('/api/product-manage/v1/register-stp-after',{
            prodIdx : this.storage_prodIdx,
            prodCd : this.storage_prodCd,
            editCd : res.data.productId,
            LinkCd : data.singleChannelProductMap.STOREFARM.id
          }).then(res => {
            alert(" ");
          })
        })
      }

      /* 
      axios.post('/api/product-manage/v1/register-stp', this.storageConvert(this.storage_check)).then(res => {
        //axios.post('/api/' ,this.registerResult(res.data);
        // alert(res.data);
        // alert(" .");
      })
      */
    }
    // #####################################################  (currentTab = 2 ) END #####################################################

  }
}

</script>

